.Add {

  h3 {
    margin-top: 20px;
  }

  hr {
    margin: 10px 0 20px;
  }
}

.Add__form {

}

%form__field {
  @include border(1px, grey);
  height: 40px;
  font-size: 1.1em;
  text-indent: 20px;
}

.form__text {
  @extend %form__field;
}

.form__price {
  @extend %form__field;
}

.form__submit {
  border: none;
  height: 40px;
  padding: 10px;
  margin: 10px 0;
  background-color: #9c88ff;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
}
