.History {

}

.History {

  hr {
    margin: 20px 0;
  }
}

%item__line {
  position: absolute;
  right: 0;
  bottom: 0;
  top: 0;
  width: 7px;
}

.item__line-green {
  @extend %item__line;
  background-color: rgb(59, 187, 59);
}

.item__line-red {
  @extend %item__line;
  background-color: rgb(204, 22, 22);
}

.items__item {
  @include shadow(2px);
  padding: 10px 10px;
  margin: 20px 0;
  position: relative;
  background-color: white;

  &:hover {
    @include shadow(5px);
  }

  &:hover .item__remove {
    opacity: 1;
  }
}

.item__price {
  position: absolute;
  right: 30px;
}

.item__remove {
  @include perfect-center();
  background-color: rgb(216, 59, 59);
  color: white;
  position: absolute;
  left: -30px;
  top: 3px;
  bottom: 3px;
  width: 30px;
  opacity: 0;
  cursor: pointer;
  transition: {
    property: opacity;
    duration: .2s;
  };
}
