.IncomeExpense {
  @include shadow(3px);
  display: flex;
  height: 100px;
  margin-bottom: 30px;
  font-weight: normal;
  background-color: white;
}

.IncomeExpense__left,
.IncomeExpense__right {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
}

.IncomeExpense__income {
  color: rgb(41, 207, 41);
  font-weight: normal;
}

.IncomeExpense__expense {
  color: rgb(190, 39, 39);
  font-weight: normal;
}
